import { registerBlockType } from '@wordpress/blocks';

import './style.scss';
import './editor.scss';

import Edit from './edit';
import Save from './save';
import metadata from './block.json';
import { __ } from '@wordpress/i18n';
import blockIcon from './icon.js';

import './list-item/index.js';

registerBlockType( metadata.name, {
	title: __( metadata.title, 'cozy-addons' ),

	description: __(
		"Elevate your lists with style using the 'Icon List' block, offering advanced options to effortlessly integrate and customize icons for a visually appealing and informative presentation.",
		'cozy-addons'
	),

	icon: {
		src: blockIcon,
	},

	example: {
		attributes: {
			cover: cozyBlockAssets.imageDir + '/preview_icon_list.jpg',
		},
		viewportWidth: 1260,
	},

	edit: Edit,

	save: Save,
} );
