import { InnerBlocks } from '@wordpress/block-editor';

function sanitizeText( str ) {
	return str.replace( /[;=()\s]/g, '' );
}

export default function Edit( { attributes } ) {
	const blockId = `cozyBlock_${ sanitizeText(
		attributes.blockClientId
	).replace( /-/gi, '_' ) }`;

	return (
		<>
			<ul
				className={ `cozy-block-icon-list ${ attributes.listStyle } ${ attributes.iconView } ${ attributes.iconLayout }` }
				id={ blockId }
			>
				<InnerBlocks.Content />
			</ul>
		</>
	);
}
