export default function Edit( { attributes } ) {
	const blockId = `cozyBlock_${ attributes.blockClientId.replace(
		/-/gi,
		'_'
	) }`;

	return (
		<>
			<div className="list-inline-block">
				{ attributes.url.length > 0 &&
					attributes.parentAttrs.linkType === 'full' && (
						<a
							href={ attributes.url }
							target={ `${
								attributes.parentAttrs.linkNewTab
									? '_blank'
									: ''
							}` }
							rel="noopener"
						>
							<li className="cozy-block-list-item" id={ blockId }>
								{ attributes.parentAttrs.enableLogo &&
									attributes.parentAttrs.iconPosition ===
										'left' && (
										<div className="list-icon-wrapper">
											<svg
												viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
												xmlns="http://www.w3.org/2000/svg"
												aria-hidden="true"
											>
												<path
													d={ attributes.iconPath }
												/>
											</svg>
										</div>
									) }
								{ attributes.parentAttrs.enableTitle && (
									<p>{ attributes.title }</p>
								) }
								{ attributes.parentAttrs.enableLogo &&
									attributes.parentAttrs.iconPosition ===
										'right' && (
										<div className="list-icon-wrapper">
											<svg
												viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
												xmlns="http://www.w3.org/2000/svg"
												aria-hidden="true"
											>
												<path
													d={ attributes.iconPath }
												/>
											</svg>
										</div>
									) }
							</li>
						</a>
					) }

				{ attributes.url.length > 0 &&
					attributes.parentAttrs.linkType === 'inline' && (
						<li className="cozy-block-list-item" id={ blockId }>
							{ attributes.parentAttrs.linkType === 'inline' && (
								<a
									href={ attributes.url }
									className="cozy-block-list-item"
									target={ `${
										attributes.parentAttrs.linkNewTab
											? '_blank'
											: ''
									}` }
									rel="noopener"
								>
									{ attributes.parentAttrs.enableLogo &&
										attributes.parentAttrs.iconPosition ===
											'left' && (
											<div className="list-icon-wrapper">
												<svg
													viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
													xmlns="http://www.w3.org/2000/svg"
													aria-hidden="true"
												>
													<path
														d={
															attributes.iconPath
														}
													/>
												</svg>
											</div>
										) }
									{ attributes.parentAttrs.enableTitle && (
										<p>{ attributes.title }</p>
									) }
									{ attributes.parentAttrs.enableLogo &&
										attributes.parentAttrs.iconPosition ===
											'right' && (
											<div className="list-icon-wrapper">
												<svg
													viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
													xmlns="http://www.w3.org/2000/svg"
													aria-hidden="true"
												>
													<path
														d={
															attributes.iconPath
														}
													/>
												</svg>
											</div>
										) }
								</a>
							) }
						</li>
					) }

				{ attributes.url.length <= 0 && (
					<li className="cozy-block-list-item" id={ blockId }>
						{ attributes.parentAttrs.enableLogo &&
							attributes.parentAttrs.iconPosition === 'left' && (
								<div className="list-icon-wrapper">
									<svg
										viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
										xmlns="http://www.w3.org/2000/svg"
										aria-hidden="true"
									>
										<path d={ attributes.iconPath } />
									</svg>
								</div>
							) }
						{ attributes.parentAttrs.enableTitle && (
							<p>{ attributes.title }</p>
						) }
						{ attributes.parentAttrs.enableLogo &&
							attributes.parentAttrs.iconPosition === 'right' && (
								<div className="list-icon-wrapper">
									<svg
										viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
										xmlns="http://www.w3.org/2000/svg"
										aria-hidden="true"
									>
										<path d={ attributes.iconPath } />
									</svg>
								</div>
							) }
					</li>
				) }
			</div>
		</>
	);
}
