import { __ } from '@wordpress/i18n';
import { InspectorControls, RichText } from '@wordpress/block-editor';
import {
	PanelBody,
	TextControl,
	BaseControl,
	Button,
} from '@wordpress/components';
import { useSelect, select, dispatch } from '@wordpress/data';
import { memo, useState, useEffect } from '@wordpress/element';
import { PremiumLink } from '../../../../components/premiumLink';

const CozyListItem = memo(
	( {
		blockId,
		attributes,
		enableTitle,
		enableLogo,
		linkType,
		iconPosition,
		setAttributes,
	} ) => {
		return (
			<>
				<div className="list-inline-block">
					{ attributes.url.length > 0 && linkType === 'full' && (
						<a>
							<li className="cozy-block-list-item" id={ blockId }>
								{ enableLogo && iconPosition === 'left' && (
									<div className="list-icon-wrapper">
										<svg
											viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
											xmlns="http://www.w3.org/2000/svg"
											aria-hidden="true"
										>
											<path d={ attributes.iconPath } />
										</svg>
									</div>
								) }
								{ enableTitle && (
									<RichText
										placeholder={ __(
											'Lorem Ipsum',
											'cozy-addons'
										) }
										value={ attributes.title }
										onChange={ ( newValue ) =>
											setAttributes( {
												...attributes,
												title: newValue,
											} )
										}
									/>
								) }
								{ enableLogo && iconPosition === 'right' && (
									<div className="list-icon-wrapper">
										<svg
											viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
											xmlns="http://www.w3.org/2000/svg"
											aria-hidden="true"
										>
											<path d={ attributes.iconPath } />
										</svg>
									</div>
								) }
							</li>
						</a>
					) }

					{ attributes.url.length > 0 && linkType === 'inline' && (
						<li className="cozy-block-list-item" id={ blockId }>
							{ linkType === 'inline' && (
								<a className="cozy-block-list-item">
									{ enableLogo && iconPosition === 'left' && (
										<div className="list-icon-wrapper">
											<svg
												viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
												xmlns="http://www.w3.org/2000/svg"
												aria-hidden="true"
											>
												<path
													d={ attributes.iconPath }
												/>
											</svg>
										</div>
									) }
									{ enableTitle && (
										<RichText
											placeholder={ __(
												'Accordion Title',
												'cozy-addons'
											) }
											value={ attributes.title }
											onChange={ ( newValue ) =>
												setAttributes( {
													...attributes,
													title: newValue,
												} )
											}
										/>
									) }
									{ enableLogo &&
										iconPosition === 'right' && (
											<div className="list-icon-wrapper">
												<svg
													viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
													xmlns="http://www.w3.org/2000/svg"
													aria-hidden="true"
												>
													<path
														d={
															attributes.iconPath
														}
													/>
												</svg>
											</div>
										) }
								</a>
							) }
						</li>
					) }

					{ attributes.url.length <= 0 && (
						<li className="cozy-block-list-item" id={ blockId }>
							{ enableLogo && iconPosition === 'left' && (
								<div className="list-icon-wrapper">
									<svg
										viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
										xmlns="http://www.w3.org/2000/svg"
										aria-hidden="true"
									>
										<path d={ attributes.iconPath } />
									</svg>
								</div>
							) }
							{ enableTitle && (
								<RichText
									placeholder={ __(
										'Accordion Title',
										'cozy-addons'
									) }
									value={ attributes.title }
									onChange={ ( newValue ) =>
										setAttributes( {
											...attributes,
											title: newValue,
										} )
									}
								/>
							) }
							{ enableLogo && iconPosition === 'right' && (
								<div className="list-icon-wrapper">
									<svg
										viewBox={ `${ attributes.iconViewBox.vx } ${ attributes.iconViewBox.vy } ${ attributes.iconViewBox.vw } ${ attributes.iconViewBox.vh }` }
										xmlns="http://www.w3.org/2000/svg"
										aria-hidden="true"
									>
										<path d={ attributes.iconPath } />
									</svg>
								</div>
							) }
						</li>
					) }
				</div>
			</>
		);
	}
);

export default function Edit( { attributes, setAttributes, clientId } ) {
	attributes.blockClientId = clientId;

	const parentBlockClientId =
		select( 'core/block-editor' ).getBlockRootClientId( clientId );
	const parentBlockAttributes = useSelect(
		( select ) =>
			select( 'core/block-editor' ).getBlockAttributes(
				parentBlockClientId
			),
		[ parentBlockClientId ]
	);

	let enableLogo = false;
	let enableTitle = true;
	let linkType = 'full';
	let linkNewTab = true;
	let iconPosition = 'left';

	if ( parentBlockAttributes ) {
		enableLogo = parentBlockAttributes.enableLogo;
		enableTitle = parentBlockAttributes.enableTitle;
		linkType = parentBlockAttributes.linkType;
		linkNewTab = parentBlockAttributes.linkNewTab;
		iconPosition = parentBlockAttributes.iconPosition;
	}

	useEffect( () => {
		const block = select( 'core/block-editor' ).getBlock( clientId );

		if ( block ) {
			const updatedAttributes = {
				...block.attributes,
				parentAttrs: {
					...block.attributes.parentAttrs,
					enableLogo: parentBlockAttributes.enableLogo,
					enableTitle: parentBlockAttributes.enableTitle,
					linkType: parentBlockAttributes.linkType,
					linkNewTab: parentBlockAttributes.linkNewTab,
					iconPosition: parentBlockAttributes.iconPosition,
				},
			};

			dispatch( 'core/block-editor' ).updateBlockAttributes(
				clientId,
				updatedAttributes
			);
		}
	}, [ parentBlockAttributes ] );

	const blockId = `cozyBlock_${ clientId.replace( /-/gi, '_' ) }`;

	const [ searchQuery, setSearchQuery ] = useState( '' );

	const handleSearch = ( e ) => {
		setSearchQuery( e.target.value );
	};

	const filteredIcons = Object.keys( cozyIcons.collection ).filter( ( key ) =>
		key.toLowerCase().includes( searchQuery.toLowerCase() )
	);

	const handleIconClick = ( dAttributeValue, viewBoxAttrVal ) => {
		// Update the 'iconPath' and 'iconViewBox' attributes using setAttributes
		const viewBoxArray = viewBoxAttrVal.split( ' ' );
		setAttributes( {
			...attributes,
			iconPath: dAttributeValue,
			iconViewBox: {
				vx: viewBoxArray[ 0 ],
				vy: viewBoxArray[ 1 ],
				vw: viewBoxArray[ 2 ],
				vh: viewBoxArray[ 3 ],
			},
		} );
	};

	return (
		<>
			<CozyListItem
				blockId={ blockId }
				attributes={ attributes }
				enableTitle={ enableTitle }
				enableLogo={ enableLogo }
				linkType={ linkType }
				iconPosition={ iconPosition }
				setAttributes={ setAttributes }
			/>

			<InspectorControls>
				<PanelBody title={ __( 'General', 'cozy-addons' ) }>
					<BaseControl>
						<BaseControl.VisualLabel>
							{ __( 'Select Icon', 'cozy-addons' ) }
						</BaseControl.VisualLabel>
						<input
							type="text"
							className="cozy-icon-search"
							placeholder={ __(
								'Search Icons...',
								'cozy-addons'
							) }
							value={ searchQuery }
							onChange={ handleSearch }
						/>
						<div className="cozy-icon-list">
							{ filteredIcons.map( ( key ) => {
								const value = cozyIcons.collection[ key ];

								// Regex pattern to match the value of the d attribute
								const regex = /d="([^"]+)"/;
								const match = value.match( regex );

								// Regular expression to match the viewBox attribute
								const viewBoxRegex = /viewBox\s*=\s*"([^"]*)"/;
								const viewBoxMatch =
									value.match( viewBoxRegex );

								const dAttributeValue = match ? match[ 1 ] : ''; // Extract the value of the d attribute
								const viewBoxAttrVal = viewBoxMatch
									? viewBoxMatch[ 1 ]
									: '';

								return (
									<Button
										key={ key }
										onClick={ () =>
											handleIconClick(
												dAttributeValue,
												viewBoxAttrVal
											)
										}
									>
										<div
											className="cozy-icon"
											title={ key }
											dangerouslySetInnerHTML={ {
												__html: value,
											} }
										/>
									</Button>
								);
							} ) }
						</div>
					</BaseControl>

					{ enableTitle && (
						<TextControl
							label={ __( 'Title', 'cozy-addons' ) }
							type="text"
							value={ attributes.title }
							onChange={ ( newValue ) =>
								setAttributes( {
									...attributes,
									title: newValue,
								} )
							}
							placeholder={ __( 'Lorem Ipsum', 'cozy-addons' ) }
						/>
					) }

					<TextControl
						label={ __( 'Url', 'cozy-addons' ) }
						type="url"
						value={ attributes.url }
						onChange={ ( newValue ) =>
							setAttributes( { ...attributes, url: newValue } )
						}
						placeholder="#"
					/>
				</PanelBody>
				{ ! cozyBlockAssets.isPremium && <PremiumLink /> }
			</InspectorControls>
		</>
	);
}
